/* Color Pallete */

$blue: #007bff;
$blue-light: #007bff10;
$blue-hover: #007bff30;
$blue-light-opaque: #eff7ff;
$blue-mid: #007bff99;
$pblue: #4c75f2;
$pblue-light: #4c75f210;
$pblue-hover: #4c75f230;
$pblue-light-opaque: #eff7ff;
$pblue-mid: #4c75f299;
$indigo: #6610f2;
$purple: #6f42c1;
$pink: #e83e8c;
$cherry: #ff073a;
$cherry-mid: #ff073a99;
$cherry-light: #ff073a20;
$cherry-hover: #ff073a30;
$cherry-light-opaque: #ffe0e6;
$red: #dc3545;
$red-light: #dc354520;
$red-mid: #dc354599;
$orange: #fd7e14;
$orange-mid: #fd7e1499;
$orange-light: #fd7e1420;
$orange-hover: #fd7e1430;
$orange-light-opaque: #ffefe2;
$yellow: #ffc107;
$yellow-light: #ffc10720;
$yellow-hover: #ffc10730;
$yellow-mid: #ffc10799;
$yellow-light-opaque: #fff7e0;
$green: #28a745;
$green-light: #28a74520;
$green-hover: #28a74530;
$green-mid: #28a74599;
$teal: #20c997;
$cyan: #17a2b8;
$white: #fff;
$gray: #6c757d;
$gray-light: #6c757d10;
$gray-hover: #6c757d20;
$gray-mid: #6c757d99;
$gray-dark: #343a40;
$gray-opaque: #f1f1f1;
$primary: #007bff;
$secondary: #6c757d;
$success: #28a745;
$info: #17a2b8;
$warning: #ffc107;
$danger: #dc3545;
$light: #f8f9fa;
$dark: #343a40;
$purple: #201aa2dd;
$purple-light: #201aa220;
$purple-hover: #201aa230;
$purple-mid: #201aa299;
$purple-light-opaque: #e3e2f3;

@mixin navbarReveal($color) {
  $animation-name: unique-id() !global;

  animation-name: $animation-name;
  animation-duration: 0.75s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: both;

  @keyframes #{$animation-name} {
    0% {
      background: transparent;
    }

    100% {
      background: $color;
    }
  }
}

@mixin levelColor($color) {
  .is-#{$color} {
    h1 {
      color: $color;
    }

    h4,
    h5 {
      color: #{$color}-mid;
    }
  }
}

body {
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  background-color: $dark;
}
.tags {
  ul {
    list-style: none;
    li {
      float: left;
      margin: 5px;
      font-family: 'Archia';
      background-color: $orange-light;
      padding: 10px;
      border-radius: 50px;
      cursor: pointer;
      color: $teal;
    }
    li:nth-child(even) {
      background-color: $orange-light;
    }
    li:nth-child(odd) {
      background-color: $green-light;
    }
  }
}
h1 {
  font-family: 'archia';
  font-size: 24px !important;
  font-weight: 900;
  text-transform: uppercase;
  color: $teal;
}

h2 {
  font-family: 'archia';
  font-size: 18px !important;
  font-weight: 400;
  text-transform: uppercase;
}

h3 {
  font-family: 'archia';
  font-size: 14px !important;
  font-weight: 400;
  text-transform: uppercase;
}

h4 {
  font-family: 'archia';
  font-size: 13px !important;
  font-weight: 900;
  text-transform: uppercase;
}

h5 {
  font-family: 'archia';
  font-size: 12px !important;
  font-weight: 900;
  text-transform: uppercase;
}

h6 {
  font-family: 'archia';
  font-size: 11px !important;
  font-weight: 600;
  text-transform: uppercase;
}

.arrow-up {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-bottom: 5px solid $gray-mid;
  margin-left: 0.25rem;
}

.arrow-down {
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 5px solid $gray-mid;
  margin-left: 0.25rem;
}

.Home {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin-left: 5rem;
  margin-right: 5rem;

  .home-left,
  .home-right {
    display: flex;
    flex-direction: column;
    width: 35rem;
  }

  .home-left {
    margin-right: 2.5rem;
  }

  .home-right {
    margin-left: 2.5rem;
  }
}

.neuglobal,
.neutable,
.neuchecker,
.neumap {
  border-radius: 30px;
  margin-bottom: 15px;
  transition: 0.5s;
  padding: 18px;
  box-shadow: 3px 3px 10px 2px rgba(0, 0, 0, 0.2),
    -3px -3px 10px 2px rgba(0, 0, 0, 0.3);
}

.neuglobal:hover,
.neutable:hover,
.neuchecker:hover,
.neumap:hover {
  box-shadow: inset 3px 3px 10px 2px rgba(0, 0, 0, 0.1),
    inset -3px -3px 10px 2px rgba(0, 0, 0, 0.3);
}

.Banner {
  display: flex;
  flex-direction: row;
  justify-content: center;
  line-height: 20px;
  font-family: 'Archia';
  font-size: 0.8rem;
  font-weight: 600;
  color: $purple;
  background: $purple-light;

  .snippet {
    align-self: center;
    width: calc(100% - 10rem);
    word-wrap: break-word;
    padding: 1.5rem;
    text-align: center;
    cursor: pointer;
  }
}

.Navbar {
  display: flex;
  flex-direction: column;
  height: 8rem;
  justify-content: space-evenly;
  text-transform: uppercase;
  font-weight: 900;

  @include navbarReveal($gray-light);

  .navbar-right {
    align-self: center;
    a {
      display: inline-block;
      font-size: 16px;
      font-family: 'Archia';
      text-decoration: none;
      margin-right: 2rem;
      color: $white;

      span {
        display: inline-block;
        transition: color 0.2s ease-in-out;

        &:hover {
          color: $gray;
        }
      }
    }
  }

  .focused {
    color: $gray;
  }

  img {
    width: 3rem;
    height: 3rem;
    align-self: center;
    cursor: pointer;
  }
}

.header {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 30rem;
  align-self: center;
  justify-content: space-between;
  padding-top: 2rem;
  margin-bottom: 1rem;

  h1,
  h6 {
    margin: 0;
  }

  h1 {
    color: $teal;
  }

  .header-mid {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    .titles {
      h6 {
        margin-top: 0.25rem;
        color: $teal;
      }
    }

    & > * {
      align-self: center;
    }
  }
}

.last-update {
  display: flex;
  flex-direction: column;
  align-self: center;
  text-align: right;
  margin-bottom: auto;
  margin-top: 0.25rem;

  h6 {
    color: $green-mid;
    font-weight: 900;
  }

  h3 {
    color: $green;
    font-weight: 600;
  }

  h3,
  h6 {
    margin: 0;
  }
}

.button {
  display: flex;
  flex-direction: row;
  padding: 0.5rem;
  border: 0;
  font-family: 'archia';
  text-transform: uppercase;
  background: $blue-light;
  color: $blue-mid;
  cursor: pointer;
  font-weight: 900;
  border-radius: 5px;
  transition: background 0.2s ease-in-out;
  margin-bottom: 0.5rem;
  margin-top: 0.25rem;
  outline: none;
  font-size: 0.75rem;
  text-decoration: none;

  svg {
    margin-right: 0.5rem;
    width: 20px;
    stroke-width: 2px;
  }

  &.is-purple {
    color: $purple-mid;
    background: $purple-light;

    &:hover {
      background: $purple-hover;
    }
  }

  &.is-green {
    color: $green-mid;
    background: $green-light;

    &:hover {
      background: $green-hover;
    }
  }

  &:hover {
    background: $blue-hover;
  }

  & > * {
    align-self: center;
  }
}

.Level {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-self: center;
  width: 30rem;

  .level-item {
    display: flex;
    flex-direction: column;

    & > * {
      align-self: center;
    }

    &:first-child {
      margin-left: 0.5rem;
    }

    &:last-child {
      margin-right: 0.5rem;
    }
  }

  h1,
  h5 {
    margin-bottom: 0;
  }

  h1 {
    font-weight: 600;
  }

  h4 {
    margin-bottom: -0.5rem;
  }

  .is-cherry {
    h1 {
      color: $cherry;
    }

    h4,
    h5 {
      color: $cherry-mid;
    }
  }

  .is-blue {
    h1 {
      color: $blue;
    }

    h4,
    h5 {
      color: $blue-mid;
    }
  }

  .is-green {
    h1 {
      color: $green;
    }

    h4,
    h5 {
      color: $green-mid;
    }
  }

  .is-gray {
    h1 {
      color: $white;
    }

    h4,
    h5 {
      color: $white;
    }
  }
}

abbr {
  text-decoration: none;

  &.is-cherry {
    color: $cherry;
  }

  &.is-blue {
    color: $blue;
  }

  &.is-green {
    color: $green;
  }

  &.is-gray {
    color: $gray-dark;
  }
}

.table-fineprint {
  text-align: right;
  margin: 0 0 0 0;
  color: $teal;
  margin-bottom: 0.25rem;
  margin-right: 0.25rem;

  a {
    text-decoration: none;
    color: $cherry;
    border-bottom: 2px solid $gray-hover;
    transition: all 0.2s ease-in-out;

    &:hover {
      background: $gray-hover;
    }
  }
}

.sticky {
  position: sticky;
}

table {
  position: relative;
  width: 30rem;
  align-self: center;
  font-family: 'archia';
  text-transform: uppercase;
  border-spacing: 3px 2px;
  border-collapse: separate;

  thead {
    background: $gray-light;
    color: $blue;
    text-align: left;
    font-size: 0.75rem;

    th {
      padding: 0.5rem;
      cursor: pointer;
      border-radius: 5px;
      transition: all 0.1s ease-in-out;
      z-index: 99;

      &.sticky {
        top: 4px;
        background: $gray-opaque;
      }

      &:hover {
        background: #ecedee;
      }

      .heading-content {
        display: flex;
        justify-content: space-between;
        position: relative;
        flex-direction: row;
        height: 0.9rem;
        align-items: center;

        abbr {
          text-align: right;
        }

        svg {
          width: 10px;
          margin: 0;
          right: 0;
          stroke-width: 4px;
          margin-top: 0.15rem;
          margin-left: 0.25rem;
          color: $blue;
        }
      }
    }
  }

  tbody {
    color: $teal;

    tr {
      transition: background 0.1s ease-in-out;
      cursor: pointer;

      &:hover {
        background: $gray-hover;
      }

      &.divider {
        height: 0.5rem;
        background: #6c757d10 !important;
      }

      &.spacer {
        height: 0.5rem;
        background: #fff !important;
      }
    }

    td {
      padding: 0.25rem;
      font-size: 0.9rem;
      border-radius: 5px;
      text-align: right;
      .deltas {
        margin-right: 0.25rem;
        font-size: 11px !important;

        svg {
          width: 9px;
          height: 9px;
          stroke-width: 3;
          vertical-align: -0.25px;
        }
      }
    }

    td:first-child {
      text-align: left;
      max-width: 7.5rem;
      word-wrap: break-word;
    }

    .state-last-update {
      background: transparent;
      transform: translateX(1rem);
      &:hover {
        background: #fff !important;
      }

      td {
        .last-update {
          text-align: left;
          display: flex;
          flex-direction: row;
          align-items: baseline;
          width: 100%;
        }
      }
    }

    .district-heading {
      transform: translateX(1rem);
      background: $gray-light;
      td {
        font-size: 0.75rem;
        font-weight: 900;
        padding: 0.45rem;
        color: $white;
        background: $gray-light;

        .heading-content {
          display: flex;
          justify-content: space-between;
          position: relative;
          flex-direction: row;
          height: 0.9rem;
          align-items: center;

          abbr {
            text-align: right;
          }

          svg {
            width: 10px;
            margin: 0;
            right: 0;
            stroke-width: 4px;
            margin-left: 0.25rem;
            margin-top: 0.15rem;
            color: $gray-mid;
          }
        }
      }
    }

    max-width: 10rem;

    .district {
      transform: translateX(1rem);
    }
  }

  .affected-count {
    position: absolute;
    margin: 0;
    top: -1rem;
    color: $teal;
    right: 0;
  }

  .is-total {
    td {
      background: $gray-hover;
    }
  }

  .dropdown {
    position: absolute;
    left: -0.75rem;
    background: $gray-light;
    border-radius: 50%;
    width: 13px;
    height: 13px;
    margin-top: 0.5rem;
    cursor: pointer;
    transition: background 0.2s ease-in-out;

    &:hover {
      background: $gray-hover;
    }

    svg {
      position: absolute;
      top: -0.35rem;
      left: 1px;
      width: 11px;
      stroke-width: 3;
    }
  }
}

#chart {
  z-index: 10;
}

.back-button {
  z-index: 11;
}

.map-default {
  stroke: none;
}

.map-hover {
  stroke: #ff073a;
  stroke-width: 2;
}

.MapExplorer {
  display: flex;
  flex-direction: column;
  align-self: center;
  width: 32.5rem;
  .header {
    display: flex;
    flex-direction: column;
    color: $teal;
    margin-bottom: 2.5rem;
    align-self: flex-start;
    h1 {
      margin-bottom: 0.25rem;
    }
  }

  .svg-parent {
    display: flex;
    flex-direction: row;
    width: 100%;
    margin-top: 1rem;

    svg {
      align-self: center;

      text {
        text-transform: uppercase;
        text-align: right;
        font-family: 'archia';
        font-weight: 600;
        fill: $gray;
        font-size: 10px;
      }
    }
  }

  .back-button {
    position: absolute;
    right: 0;
    margin-right: 15px;
    background: $orange-light;
    color: $orange;
    transition: all 0.2s ease-in-out;
    &:hover {
      background: $orange-hover;
    }
  }

  .meta {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 1rem;

    .district-last-update {
      position: absolute;
      right: 0;
      bottom: 0;
    }

    h2 {
      margin: 0;
      color: #e23028;
      word-wrap: break-word;
      font-weight: 900;
    }

    h4 {
      margin: 0;
      color: $gray-mid;
      word-wrap: break-word;
    }

    .unknown {
      position: absolute;
      top: 17rem;
      width: 5rem;
    }
  }

  .map-stats {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 5rem;
    position: relative;

    h1,
    h5,
    h6,
    h3 {
      margin: 0;
    }

    h3 {
      color: $teal;
      font-weight: 900;
    }

    .stats {
      background: $cherry-light;
      color: $cherry;
      padding: 0.5rem;
      border-radius: 5px;
      display: flex;
      flex-direction: column;
      margin-left: 0.25rem;
      margin-right: 0.25rem;
      margin-bottom: 0.5rem;
      width: 100%;

      .stats-bottom {
        display: flex;
        flex-direction: row;

        h6 {
          margin-top: auto;
          margin-bottom: 0.25rem;
        }
      }

      h1 {
        font-weight: 600;
        color: $cherry;
      }

      &.is-green {
        background: $green-light;
        h5,
        h6 {
          color: $green-mid;
        }

        h1 {
          color: $green;
        }
      }

      &.is-blue {
        background: $blue-light;
        h5,
        h6 {
          color: $blue-mid;
        }

        h1 {
          color: $blue;
        }
      }

      &.is-gray {
        background: $gray-light;
        h5,
        h6 {
          color: $white;
        }

        h1 {
          color: $white;
        }
      }

      &.is-yellow {
        background: $warning;
        h5,
        h6 {
          color: $white;
        }

        h1 {
          color: $white;
        }
      }

      &:first-child {
        margin-left: 0;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
.global-stats {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 5rem;
  position: relative;

  h1,
  h2,
  h5,
  h6,
  h3 {
    margin: 0;
  }

  h3 {
    color: $gray-mid;
    font-weight: 900;
  }

  .stats {
    background: $cherry-light;
    color: $cherry;
    padding: 0.5rem;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    margin-left: 0.2rem;
    margin-right: 0.25rem;
    margin-bottom: 0.5rem;
    width: 100%;

    .stats-bottom {
      display: flex;
      flex-direction: row;

      h6 {
        margin-top: auto;
        margin-bottom: 0.25rem;
      }
    }

    h3 {
      font-weight: 600;
      color: $cherry;
    }

    &.is-green {
      background: $green-light;
      h5,
      h6 {
        color: $green-mid;
      }

      h3 {
        color: $green;
      }
    }

    &.is-blue {
      background: $blue-light;
      h5,
      h6 {
        color: $blue-mid;
      }

      h3 {
        color: $blue;
      }
    }

    &.is-gray {
      background: $gray-light;
      h5,
      h6 {
        color: $white;
      }

      h3 {
        color: $white;
      }
    }

    &.is-yellow {
      background: $warning;
      h5,
      h6 {
        color: $white;
      }

      h2 {
        color: $white;
      }
    }

    &:first-child {
      margin-left: 0;
    }

    &:last-child {
      margin-right: 0;
    }
  }
}
.tabs {
  position: relative;
  z-index: 99;
  display: flex;
  flex-direction: row;

  .tab {
    min-width: 5rem;
    background: $gray-light;
    padding-left: 1rem;
    padding-right: 1rem;
    color: $gray-mid;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    cursor: pointer;
    text-align: center;
    margin-right: 0.25rem;
    transition: all 0.3s ease-in-out;

    &:hover {
      background: $gray-hover;
    }

    &.focused {
      color: $gray;
      background: $gray-opaque;
    }
  }
}

.TimeSeries-Parent {
  display: flex;
  flex-direction: column;
  width: 30rem;
  align-self: center;
}

.timeseries-header {
  h1 {
    color: $gray-dark;
    margin: 0;
    margin-bottom: 0.5rem;
  }

  width: 30rem;
  align-self: center;
  margin-top: 1rem;

  .scale-modes {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    bottom: 0;
    right: 0;
    z-index: 99;
    color: $gray;
    margin-top: 1rem;
    label {
      font-family: 'archia';
      text-transform: uppercase;
      font-size: 0.75rem;
      font-weight: 900;
      z-index: 99;
      color: $gray-mid;
    }

    & > * {
      align-self: center;
    }

    .timeseries-mode,
    .timeseries-logmode {
      margin-left: 1rem;
      display: flex;
      flex-direction: row;
      input.switch {
        position: relative;
        appearance: none;
        outline: none;
        margin: 0;
        cursor: pointer;
        width: 24px;
        height: 14px;
        background-color: #fff;
        border: 2px solid #d9dadc;
        border-radius: 10px;
        transition: all 200ms ease-in-out;
      }

      input.switch::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 10px;
        height: 10px;
        background-color: $gray-mid;
        border-radius: 50%;
      }

      input.switch:checked {
        background-color: $gray-mid;
        transition: all 200ms ease-in-out;
      }

      input.switch:checked::after {
        left: 10px;
        background-color: $light;
      }

      label {
        font-size: 0.75rem;
        margin-right: 0.2rem;
        padding-bottom: 0;
        color: $gray;
      }
    }

    .disabled {
      input.switch {
        background: $gray-light;
        border: $gray-light 2px solid;
        cursor: not-allowed;
      }

      input.switch::after {
        background: $gray-light;
      }
    }
  }
}

.timeseries,
.Minigraph {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  width: 100%;

  .stats {
    padding: 0.25rem;
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
    width: 5rem;
    border-radius: 3px;
    display: flex;
    flex-direction: column;

    h2,
    h5,
    h6 {
      margin: 0;
      color: $cherry-mid;
    }

    h2,
    h6 {
      color: $cherry;
    }

    .stats-bottom {
      display: flex;
      flex-direction: row;

      h6 {
        margin-bottom: 0.1rem;
        margin-left: 0.25rem;
      }

      & > * {
        margin-top: auto;
      }
    }

    &.is-green {
      h5 {
        color: $green-mid;
      }

      h2,
      h6 {
        color: $green;
      }
    }

    &.is-gray {
      h5 {
        color: $gray-mid;
      }

      h2,
      h6 {
        color: $gray;
      }
    }

    &.is-blue {
      h5 {
        color: $blue-mid;
      }

      h2,
      h6 {
        color: $blue;
      }
    }
  }

  .svg-parent {
    position: relative;
    display: flex;
    width: 30rem;
    align-self: center;
    background: $cherry-light;
    border-radius: 5px;
    margin-bottom: 1rem;
    height: 10rem;

    svg {
      .domain,
      .tick,
      line {
        stroke: $cherry;
        stroke-width: 2;
      }

      text {
        font-family: 'archia';
        text-transform: uppercase;
        color: $cherry-mid;
        stroke: transparent;
        font-size: 12px;
      }
    }

    &.is-green {
      background: $green-light;

      svg {
        .domain,
        .tick,
        line {
          stroke: $green;
        }

        text {
          color: $green-mid;
          stroke: transparent;
        }
      }
    }

    &.is-gray {
      background: $gray-light;

      .domain,
      .tick,
      line {
        stroke: $gray;
      }

      text {
        color: $gray-mid;
        stroke: transparent;
      }
    }
  }
}

